import React from 'react';
import { Field, reduxForm } from 'redux-form';
import moment from 'moment';
import axios from 'axios';
import Grid from '@material-ui/core/Grid';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import { href } from '../utils';
import {
  renderField,
  renderDatePicker,
  renderTimePicker,
  renderTextarea,
} from './form-component';

const required = value => (value ? undefined : 'Required');

const GoalNew = ({
  handleSubmit, goal_id, refresh, open, handleClose,
}) => {
  const onSubmit = (values) => {
    const newValues = { ...values };
    if (values.date) {
      newValues.date = moment(values.date).format('YYYY-MM-DD');
      if (values.time) {
        newValues.time = moment(values.time).format('HH:mm:ss');
      }
    }
    if (goal_id) {
      newValues.parent = goal_id;
    }
    if (values.total_time) {
      newValues.total_time = values.total_time * 60;
    }
    if (values.total_spend) {
      newValues.total_spend = values.total_spend * 60;
    }
    axios({
      method: 'post',
      url: `${href}api/goal/`,
      data: newValues,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then(() => {
      if (refresh) {
        refresh();
      }
      handleClose();
    });
  };

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="Add new goal"
      open={open}
      style={{ direction: 'rtl', textAlign: 'right' }}
      scroll="body"
    >
      <DialogTitle>إضافة هدف جديد</DialogTitle>
      <form onSubmit={handleSubmit(onSubmit)} style={{ padding: 12 }}>
        <Grid container spacing={24}>
          <Grid item xs={12}>
            <Field
              name="title"
              component={renderField}
              type="text"
              label="الهدف"
              validate={required}
            />
          </Grid>

          <Grid item xs={12}>
            <Field
              name="description"
              component={renderTextarea}
              label="التفاصيل"
            />
          </Grid>

          <Grid item sm={6}>
            <Field
              name="total_time"
              component={renderField}
              type="number"
              label="الوقت الكلى بالساعات"
            />
          </Grid>
          <Grid item sm={6}>
            <Field
              name="total_spend"
              component={renderField}
              type="number"
              label="الوقت المنقضى بالساعات"
            />
          </Grid>

          <Grid item sm={6}>
            <Field
              name="date"
              component={renderDatePicker}
              label="تاريخ الانتهاء"
            />
          </Grid>
          <Grid item sm={6}>
            <Field
              name="time"
              component={renderTimePicker}
              label="وقت الانتهاء"
            />
          </Grid>

          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Submit
            </Button>
          </Grid>
        </Grid>
      </form>
    </Dialog>
  );
};

export default reduxForm({
  form: 'GoalNewForm',
})(GoalNew);
