import React, { useState } from 'react';
import SwipeableViews from 'react-swipeable-views';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import green from '@material-ui/core/colors/green';
import CommentList from './comment_list';
import Subgoals from './subgoals';
import Tasks from './tasks';

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
    position: 'relative',
    minHeight: 200,
    direction: 'rtl',
    textAlign: 'right',
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
  fabGreen: {
    color: theme.palette.common.white,
    backgroundColor: green[500],
  },
});

const GoalTabs = ({
  classes, theme, goal_id, history,
}) => {
  const [value, setValue] = useState(0);

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={(e, v) => setValue(v)}
          indicatorColor="primary"
          textColor="primary"
          fullWidth
        >
          <Tab label="Comments" style={{ outline: 'none' }} />
          <Tab label="Subgoals" style={{ outline: 'none' }} />
          <Tab label="Tasks" style={{ outline: 'none' }} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={setValue}
      >
        <CommentList goal_id={goal_id} />
        <Subgoals id={goal_id} />
        <Tasks goal={goal_id} history={history} />
      </SwipeableViews>
    </div>
  );
};

export default withStyles(styles, { withTheme: true })(GoalTabs);
