import React, { useState, useEffect } from 'react';
import _ from 'lodash';
import { ListGroup, ListGroupItem } from 'reactstrap';
import Link from 'react-router-dom/Link';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import { href } from '../utils';
import GoalNew from './GoalNew';

const styles = {
  ListGroup: {
    textAlign: 'right',
    direction: 'rtl',
    margin: '20px 0',
  },
  button: {
    marginBottom: 30,
  },
};

const Subgoals = ({ id }) => {
  const [goals, setGoals] = useState(null);
  const [addNewGoal, setAddNewGoal] = useState(false);

  const fetchGoals = () => {
    axios({
      method: 'get',
      url: `${href}api/goal/${id}/subgoals/`,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setGoals(_.mapKeys(response.data, 'id'));
    });
  };

  useEffect(() => {
    fetchGoals();
  }, []);

  // const time_h = parseInt(goal.total_time / 60, 10);
  // const time_m = goal.total_time - time_h * 60;
  const renderList = () => (
    <ListGroup style={styles.ListGroup}>
      {_.map(goals, goal => (
        <Link to={`/goal/goal/${goal.id}`} key={goal.id}>
          <ListGroupItem className="justify-content-between">
            {goal.title}
            {/* <Badge className="float-left" pill>
              {`${time_h}:${time_m}`}
            </Badge> */}
          </ListGroupItem>
        </Link>
      ))}
    </ListGroup>
  );

  if (!goals) {
    return <h2>Loading...</h2>;
  }
  return (
    <div>
      {renderList()}
      <Button
        variant="contained"
        color="primary"
        style={{ float: 'left' }}
        onClick={() => setAddNewGoal(true)}
      >
        Add Subgoal
      </Button>
      <GoalNew
        goal_id={id}
        refresh={fetchGoals}
        open={addNewGoal}
        handleClose={() => setAddNewGoal(false)}
      />
    </div>
  );
};

export default Subgoals;
