import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import withRouter from 'react-router-dom/withRouter';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import { DateTime } from '../date_time';
import GoalTabs from '../goal_tabs';
import { fetchGoal } from '../../actions/index';

const styles = () => ({
  paper: {
    marginTop: 10,
    padding: 10,
    marginBottom: 10,
    marginLeft: 'auto',
    marginRight: 'auto',
    maxWidth: 900,
    width: '95%',
    textAlign: 'right',
    direction: 'rtl',
  },
  goalInfo: {
    padding: 10,
    marginBottom: 10,
    marginLeft: 10,
    marginRight: 10,
  },
  title: {
    paddingBottom: 20,
    fontWeight: '700',
  },
  description: {
    padding: 10,
    marginBottom: 20,
    fontSize: '1rem',
  },
});

const Goal = ({
  match, fetchGoal, goal, classes, history,
}) => {
  useEffect(() => {
    const { id } = match.params;
    fetchGoal(id);
  }, []);

  useEffect(() => {
    const { id } = match.params;
    fetchGoal(id);
  }, [match.params.id]);

  if (!goal) {
    return <div>Loading...</div>;
  }

  const { id } = match.params;
  return (
    <Paper className={classes.paper}>
      <div className={classes.goalInfo}>
        <Typography variant="h4" className={classes.title}>
          {goal.title}
        </Typography>
        <Typography
          variant="body1"
          component="pre"
          className={classes.description}
        >
          {goal.description}
        </Typography>
        <Grid container justify="space-between">
          <Grid item>
            {' الوقت الكلى'}
            <span className="badge badge-primary badge-pill">
              {`${parseInt(goal.total_time / 60, 10)}h`}
            </span>
          </Grid>
          <Grid item>
            {'الوقت المنقضى'}
            <span className="badge badge-primary badge-pill">
              {`${parseInt(goal.total_spend / 60, 10)}h`}
            </span>
          </Grid>
          <Grid item>
            {'تاريخ الانتهاء'}
            <span className="badge badge-primary badge-pill">
              <DateTime dateTime={goal.finished_at} />
            </span>
          </Grid>
        </Grid>
      </div>
      <GoalTabs goal_id={id} history={history} />
    </Paper>
  );
};

function mapStateToProps({ goals }, ownProps) {
  return { goal: goals[ownProps.match.params.id] };
}

export default withRouter(
  connect(
    mapStateToProps,
    { fetchGoal },
  )(withStyles(styles)(Goal)),
);
