import React from 'react';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import { href } from '../utils';


const styles = theme => ({
    paper: {
        position: 'absolute',
        width: theme.spacing.unit * 50,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4,
        borderRadius: 10,
    },
});


class LoginForm extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            username: '',
            password: '',
            open: false,
        }
    }

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    handleSubmit = (event) => {
        event.preventDefault();
        axios.post(`${href}api/login/`,
            this.state).then((response) => {
            var values = response.data;
            localStorage.setItem('token', values.token);
            window.location.reload()
        });
    };

    handleChange = e => {
        const name = e.target.name;
        const value = e.target.value;
        this.setState(prevState => {
            const newState = { ...prevState };
            newState[name] = value;
            return newState;
        });
    };

    render() {
        const { classes } = this.props;
        return (
            <div>
                <Button onClick={this.handleOpen}>Login</Button>
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                >
                    <div style={{top: '35%', left:'35%'}} className={classes.paper}>
                        <Typography variant="h6" id="modal-title">
                            Login
                        </Typography>
                        <Typography variant="subtitle1" id="simple-modal-description">
                            <form>
                                <div className="form-group">
                                    <input
                                        required
                                        placeholder="username"
                                        className="form-control"
                                        name="username"
                                        type="text"
                                        value={this.state.username}
                                        onChange={this.handleChange}
                                    />
                                </div>
                                <div className="form-group">
                                    <input
                                        required
                                        placeholder="password"
                                        className="form-control"
                                        name="password"
                                        type="password"
                                        value={this.state.password}
                                        onChange={this.handleChange}
                                    />
                                </div>
                                <Button onClick={this.handleSubmit} type="submit" variant="contained" color="primary">
                                    Login
                                </Button>
                            </form>
                        </Typography>
                    </div>
                </Modal>
            </div>
        );
    }

}

export default withStyles(styles)(LoginForm);