import { combineReducers } from 'redux';
import { reducer as formReducer } from 'redux-form';
import goalsReducers from './reducer-goals';
import daysReducer from './reducer-days';
import errors from '../components/common/error/state';

const allReducers = combineReducers({
  form: formReducer,
  goals: goalsReducers,
  days: daysReducer,
  errors,
});

export default allReducers;
