import React, { useState, useEffect } from 'react';
import axios from 'axios';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import Fab from '@material-ui/core/Fab';
import SendIcon from '@material-ui/icons/Send';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import _ from 'lodash';
import moment from 'moment';
import { href } from '../utils';

const styles = theme => ({
  root: {
    direction: 'rtl',
    marginTop: 10,
  },
  fab: {
    margin: theme.spacing.unit,
    width: 48,
  },
  textField: {
    margin: theme.spacing.unit,
  },
});

const CommentList = ({ goal_id, classes }) => {
  const [comments, setComments] = useState({});
  const [newComment, setNewComment] = useState('');

  useEffect(() => {
    axios({
      method: 'get',
      url: `${href}api/comment/`,
      params: {
        goal_id,
      },
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setComments(_.mapKeys(response.data, 'id'));
    });
  }, []);

  const addComment = () => {
    axios({
      method: 'post',
      url: `${href}api/comment/`,
      data: {
        content: newComment,
        goal_id,
      },
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setComments({ ...comments, [response.data.id]: response.data });
      setNewComment('');
    });
  };

  const deleteComment = (id) => {
    axios({
      method: 'delete',
      url: `${href}api/comment/${id}/`,
      params: {
        goal_id,
      },
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setComments(_.omit(comments, id));
    });
  };

  const renderList = () => (
    <List className={classes.root}>
      {_.map(comments, comment => (
        <React.Fragment>
          <ListItem style={{ paddingLeft: 0, paddingRight: 0 }}>
            <ListItemText
              style={{ textAlign: 'right', paddingRight: 20 }}
              primaryTypographyProps={{ component: 'pre' }}
              primary={comment.content}
              secondaryTypographyProps={{ style: { direction: 'ltr' } }}
              secondary={moment(comment.created_at).format(
                'DD-MM-YYYY hh:mm a',
              )}
            />
            <IconButton
              aria-label="Delete"
              onClick={() => deleteComment(comment.id)}
            >
              <DeleteIcon />
            </IconButton>
          </ListItem>
          <Divider />
        </React.Fragment>
      ))}
    </List>
  );

  return (
    <div className={classes.root}>
      {renderList()}
      <div style={{ display: 'flex', direction: 'rtl', alignItems: 'center' }}>
        <Fab
          color="primary"
          aria-label="Add"
          onClick={addComment}
          className={classes.fab}
          size="medium"
        >
          <SendIcon />
        </Fab>
        <TextField
          className={classes.textField}
          multiline
          fullWidth
          variant="outlined"
          value={newComment}
          onChange={e => setNewComment(e.target.value)}
        />
      </div>
    </div>
  );
};

export default withStyles(styles)(CommentList);
