import axios from 'axios';
import { href } from '../utils';
import { ErrorsActions } from '../components/common/error/state';

const ROOT_URL = `${href}api`;

export function fetchGoals() {
  const request = axios.get(`${ROOT_URL}/goal/`, {
    headers: {
      Authorization: `Token ${localStorage.getItem('token')}`,
    },
    params: {
      main: true,
    },
  });

  return (dispatch) => {
    request
      .then((response) => {
        dispatch({
          type: 'FETCH_GOALS',
          payload: response.data,
        });
      })
      .catch((error) => {
        dispatch(ErrorsActions.addError(String(error)));
      });
  };
}

export function fetchGoal(id) {
  const request = axios.get(`${ROOT_URL}/goal/${id}/`, {
    headers: {
      Authorization: `Token ${localStorage.getItem('token')}`,
    },
  });

  return (dispatch) => {
    request.then((response) => {
      dispatch({
        type: 'FETCH_GOAL',
        payload: response.data,
      });
    }).catch((error) => {
      dispatch(ErrorsActions.addError(String(error)));
    });
  };
}

export function fetchDays() {
  const request = axios({
    method: 'get',
    url: `${ROOT_URL}/days/`,
    headers: {
      Authorization: `Token ${localStorage.getItem('token')}`,
    },
  });

  return (dispatch) => {
    request.then((response) => {
      dispatch({
        type: 'FETCH_DAYS',
        payload: response.data,
      });
    }).catch((error) => {
      dispatch(ErrorsActions.addError(String(error)));
    });
  };
}
