import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';

import { BrowserRouter as Router, Route } from 'react-router-dom';
import NavBar from './components/reactstrap/navbar';
import Task from './components/reactstrap/task';
import Goals from './components/Goals';
import { Goal } from './components';
import Day from './components/day';
import task_new from './components/task_new';
import DaysList from './components/days';
import GoalUpdate from './components/goal_update';
import MyCalendar from './components/calendar';
import GoalTabs from './components/goal_tabs';
import ErrorDialog from './components/common/error/components/ErrorDialog';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#4464af',
    },
    secondary: {
      main: '#dc3545',
    },
  },
  direction: 'rtl',
});

const App = () => (
  <MuiThemeProvider theme={theme}>
    <Router>
      <React.Fragment>
        <header>
          <NavBar />
        </header>
        <ErrorDialog />
        <Route exact path="/" render={props => <Goals {...props} />} />
        <Route exact path="/goal/goal" render={props => <Goals {...props} />} />
        <Route path="/goal/goal/:id" render={props => <Goal {...props} />} />
        <Route
          path="/goal/update/:id"
          render={props => <GoalUpdate {...props} />}
        />
        <Route
          path="/goal/create_task"
          render={props => <task_new {...props} />}
        />
        <Route path="/days" render={props => <DaysList {...props} />} />
        <Route path="/day/:id" render={props => <Day {...props} />} />
        <Route path="/calendar" render={props => <MyCalendar {...props} />} />
        <Route path="/goal-tabs" render={props => <GoalTabs {...props} />} />
        <Route path="/task/:id" render={props => <Task {...props} />} />
      </React.Fragment>
    </Router>
  </MuiThemeProvider>
);

export default App;
