import React, { useState, useEffect } from 'react';
import { Field, reduxForm } from 'redux-form';
import moment from 'moment';
import axios from 'axios';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import {
  renderField,
  renderDatePicker,
  renderTimePicker,
  renderTextarea,
  renderSelectField,
} from './form-component';
import { href } from '../utils';

const required = value => (value ? undefined : 'Required');

const TaskNew = ({
  open,
  handleClose,
  initGoal,
  initDay,
  initDate,
  initType,
  change,
  handleSubmit,
  refresh,
}) => {
  const [goals, setGoals] = useState(null);

  useEffect(() => {
    axios({
      method: 'get',
      url: `${href}api/get-user-goals/`,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setGoals(response.data);
      if (initGoal) {
        change('goal', initGoal);
      }
      if (initDay) {
        const date = new Date(initDate);
        change('date', date);
      }
      if (initType) {
        change('task_type', initType);
      }
    });
  }, []);

  useEffect(() => {
    if (initType) {
      change('task_type', initType);
    }
  }, [open]);

  const onSubmit = (values) => {
    const newValues = { ...values };
    if (values.date) {
      newValues.date = moment(values.date).format('YYYY-MM-DD');
      if (values.time) {
        newValues.stime = moment(values.time).format('HH:mm:ss');
      }
    }
    axios({
      method: 'post',
      url: `${href}api/task/`,
      data: newValues,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then(() => {
      if (refresh) {
        refresh();
      }
      handleClose();
    });
  };

  const renderGoals = () => goals.map(goal => (
    <option key={goal.id} value={goal.id}>
      {goal.title}
    </option>
  ));

  if (!goals) {
    return <h2>Loading...</h2>;
  }

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="add new task"
      open={open}
      style={{ direction: 'rtl', textAlign: 'right' }}
      scroll="body"
    >
      <DialogTitle>إضافة مهمة جديد</DialogTitle>
      <form onSubmit={handleSubmit(onSubmit)} style={{ padding: 12 }}>
        <Grid container spacing={24}>
          <Grid item xs={12}>
            <Field
              name="title"
              component={renderField}
              type="text"
              label="المهمة"
              validate={required}
            />
          </Grid>

          <Grid item sm={6}>
            <Field name="date" component={renderDatePicker} label="التاريخ " />
          </Grid>
          <Grid item sm={6}>
            <Field name="time" component={renderTimePicker} label="الوقت" />
          </Grid>

          <Grid item sm={6}>
            <Field
              name="duration"
              component={renderField}
              type="number"
              label="المدة بالدقائق"
            />
          </Grid>
          <Grid item sm={6}>
            <Field
              name="task_type"
              component={renderSelectField}
              label="task type"
            >
              <option value="" />
              <option key="task_type#1" value="a">
                {'تخطيط'}
              </option>
              <option key="task_type#2" value="b">
                {'تنفيذ'}
              </option>
            </Field>
          </Grid>

          <Grid item xs={12}>
            <Field
              name="description"
              component={renderTextarea}
              label="التفاصيل"
            />
          </Grid>

          <Grid item xs={12}>
            <Field name="goal" component={renderSelectField} label="تتبع هدف">
              <option value="" />
              {renderGoals()}
            </Field>
          </Grid>
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary">
              Submit
            </Button>
          </Grid>
        </Grid>
      </form>
    </Dialog>
  );
};

export default reduxForm({
  form: 'TaskNewForm',
})(TaskNew);
