import React from 'react';
import moment from 'moment';

export function Time(props) {
    let minutes = parseInt(props.time, 10);
    let hh = Math.floor(minutes/60);
    let mm = minutes - (hh*60);
    const style = {
        direction: 'ltr'
    }
    return (
        <div style={style}>
            {((hh > 9) ? hh: `0${hh}`) + ':' + ((mm > 9) ? mm: `0${mm}`)}
        </div>
    );
}


export function DateTime(props) {
    const style = {
        direction: 'ltr'
    }
    return (
        <div style={style}>
            {moment(props.dateTime).format('YYYY-MM-DD hh:mm a')}
        </div>
    );
}