import React, { useState, useEffect } from 'react';
import axios from 'axios';
import _ from 'lodash';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { href } from '../utils';
import TaskNew from './task_new';

const Tasks = ({
  goal, day, date, history,
}) => {
  const [tasks, setTasks] = useState(null);
  const [newTypeA, setNewTypeA] = useState(false);
  const [newTypeB, setNewTypeB] = useState(false);

  const fetchTasks = () => {
    const params = day ? { day } : { goal };
    axios({
      method: 'get',
      url: `${href}api/task/`,
      params,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setTasks(_.mapKeys(response.data, 'id'));
    });
  };

  useEffect(() => {
    fetchTasks();
  }, []);

  const renderList = type => (
    <List>
      {_.map(tasks, (task) => {
        if (task.task_type === type) {
          return (
            <ListItem
              style={{ textAlign: 'right' }}
              button
              onClick={() => history.push(`/task/${task.id}`)}
              key={task.id}
            >
              <ListItemText
                primary={task.title}
                primaryTypographyProps={{ color: 'primary' }}
              />
            </ListItem>
          );
        }
        return null;
      })}
    </List>
  );

  if (!tasks) {
    return <h2>Loading...</h2>;
  }

  return (
    <Grid
      container
      spacing={16}
      style={{ direction: 'rtl', textAlign: 'right', marginTop: 10 }}
    >
      <Grid item sm={6}>
        <Typography variant="h4">التخطيط</Typography>
        <ul className="list-group list">{renderList('a')}</ul>
        <Button
          variant="contained"
          color="secondary"
          style={{ marginTop: 10, float: 'left' }}
          onClick={() => setNewTypeA(true)}
        >
          Add Task
        </Button>
        <TaskNew
          open={newTypeA}
          handleClose={() => setNewTypeA(false)}
          initType="a"
          initGoal={goal}
          initDay={day}
          initDate={date}
          refresh={fetchTasks}
        />
      </Grid>
      <Grid item sm={6}>
        <Typography variant="h4">التنفيذ</Typography>
        <ul className="list-group list">{renderList('b')}</ul>
        <Button
          variant="contained"
          color="primary"
          style={{ marginTop: 10, float: 'left' }}
          onClick={() => setNewTypeB(true)}
        >
          Add Task
        </Button>
        <TaskNew
          open={newTypeB}
          handleClose={() => setNewTypeB(false)}
          initType="b"
          initGoal={goal}
          initDay={day}
          initDate={date}
          refresh={fetchTasks}
        />
      </Grid>
    </Grid>
  );
};

export default Tasks;
