import React from 'react';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import { DateTime } from '../date_time';
import { href } from '../../utils';


const styles = theme => ({
    root: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
        marginTop: 20
    },
    chip: {
        margin: theme.spacing.unit,
    },
});


class Task extends React.Component {

    state = {
        task: null
    };

    componentDidMount() {
        axios({
            method: 'get',
            url: `${href}api/task/${this.props.match.params.id}/`,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(response => {
            this.setState({
                task: response.data
            });
        });
    }

    render() {
        const { classes } = this.props;
        if(!this.state.task) {
            return <h2>Loading...</h2>
        }
        return (
            <div className="container" style={{direction: "rtl", textAlign: "right"}}>
            <Paper className={classes.root} elevation={1}>
                <Typography variant="h4" component="h4">
                    {this.state.task.title}
                </Typography>
                <Typography component="p">
                    {this.state.task.description}
                </Typography>
                <Chip
                    label={this.state.task.task_type}
                    className={classes.chip}
                    color="primary"
                />
                <Chip
                    label={this.state.task.duration}
                    className={classes.chip}
                    color="primary"
                />
                <Chip
                    label={<DateTime dateTime={this.state.task.time} />}
                    className={classes.chip}
                    color="primary"
                />
            </Paper>
            </div>
        );
    }
}

export default withStyles(styles)(Task);