import React from 'react';
import {
    ListGroup,
    ListGroupItem
} from 'reactstrap';
import Link from "react-router-dom/Link";
import { connect } from 'react-redux';
import _ from 'lodash';
import { fetchDays } from '../actions/index';


class DaysList extends React.Component {

    componentDidMount() {
        this.props.fetchDays();
    }

    renderList = () => {
        return _.map(this.props.days, day => {
            return (
                <Link to={`/day/${day.id}`} key={day.id}>
                    <ListGroupItem className="justify-content-between">{day.date}</ListGroupItem>
                </Link>
            );
        });
    };

    render() {
        return (
            <div className="container" style={{direction: "rtl", textAlign: "right"}}>
                <br />
                <br />
                <ListGroup>
                    {this.renderList()}
                </ListGroup>
                <br />
                <br />

            </div>
        );
    }
}

function mapStateToProps(state){
    return {days: state.days}
}

export default connect(mapStateToProps, {fetchDays})(DaysList);