import React from 'react';
import {
    Field,
    reduxForm
} from 'redux-form';
import { renderField, renderDatePicker, renderTimePicker, renderTextarea } from './form-component';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import moment from 'moment';
import axios from 'axios';
import { fetchGoal } from "../actions";
import { connect } from "react-redux";
import { href } from '../utils';


class GoalUpdate extends React.Component {

    componentDidMount() {
        const { id } = this.props.match.params;
        this.props.fetchGoal(id);
    }

    componentDidUpdate(prevProps) {
        if(this.props.goal !== prevProps.goal) {
            var goal = this.props.goal;

            this.props.initialize({
                title: goal.title,
                description: goal.description,
                total_time: goal.total_time,
                total_spend: goal.total_spend,
                date: new Date(goal.finished_at),
                time: new Date(goal.finished_at)
            });
        }
    }

    onSubmit = (values) => {
        console.log('entered on submit');
        let date = moment(values.date).format("YYYY-MM-DD");
        let time = moment(values.time).format("HH:mm:ss");
        let newValues = {...values, date, time};
        const { id } = this.props.match.params;
        axios({
            method: 'put',
            url: `${href}api/goal/${id}/`,
            data: newValues,
            headers: {'Authorization': 'Token 560ada8c525156d6441fec29ea2910571eb1f35c'}})
            .then((response) => {
                this.props.history.push(`/goal/goal/${id}`);
            });
    }

    render(){
        var goal = this.props.goal;
        if(!goal) {
            return <div>Loading...</div>;
        }
        const { handleSubmit } = this.props;

        return (
            <MuiThemeProvider>
                <div className="container" style={{direction: "rtl", textAlign: "right"}}>
                <form onSubmit={ handleSubmit(this.onSubmit) }>
                    <h3>تعديل الهدف</h3>
                    <Field name="title" component={renderField} type="text" label="الهدف" validate={required} />

                    {/* <div className="form-group">
                        <label>التفاصيل</label>
                        <Field name="description" component="textarea" className="form-control" type="text" />
                    </div> */}

                    <Field name="description" component={renderTextarea} label="التفاصيل" validate={required} />
                    <Field name="total_time" component={renderField} type="number" label="الوقت الكلى بالساعات" validate={required} />
                    <Field name="total_spend" component={renderField} type="number" label="الوقت المنقضى بالساعات" validate={required} />
                    <Field name="date" component={renderDatePicker} label="تاريخ الانتهاء"/>
                    <Field name="time" component={renderTimePicker} label="وقت الانتهاء"/>
                    <button type="submit" className="btn btn-primary">Submit</button>
                    <br />
                    <br />
                </form>
                </div>
            </MuiThemeProvider>
        );
    }
}

const required = value => value ? undefined : 'Required';

function mapStateToProps({ goals }, ownProps) {
    return {goal: goals[ownProps.match.params.id]};
}

GoalUpdate = connect(mapStateToProps, {fetchGoal})(GoalUpdate);


export default reduxForm({
    form: 'GoalUpdateForm',
    // initialValues: {date: new Date("1995-09-05")}
})(GoalUpdate);