import React, { useState, useEffect } from 'react';
import _ from 'lodash';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
// import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import withStyles from '@material-ui/core/styles/withStyles';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import { fetchGoals } from '../actions/index';
import GoalNew from './GoalNew';

const styles = theme => ({
  root: {
    margin: 10,
    maxWidth: 1000,
    marginLeft: 'auto',
    marginRight: 'auto',
    width: '95%',
  },
  list: {
    direction: 'rtl',
    paddingTop: 0,
    paddingBottom: 0,
    margin: '20px 0',
  },
  listItem: {
    backgroundColor: theme.palette.background.paper,
    textAlign: 'right',
    borderRadius: '.25rem',
    paddingBottom: 12,
    paddingTop: 12,
    marginTop: 2,
    marginBottom: 2,
  },
});

const Goals = ({
  goals, fetchGoals, history, classes,
}) => {
  const [addGoalOpen, setAddGoalOpen] = useState(false);
  useEffect(() => {
    fetchGoals();
  }, []);

  // const time_h = parseInt(goal.total_time / 60, 10);
  // const time_m = goal.total_time - time_h * 60;
  const renderList = () => (
    <List className={classes.list}>
      {_.map(goals, goal => (
        <ListItem
          className={classes.listItem}
          key={goal.id}
          button
          onClick={() => history.push(`/goal/goal/${goal.id}`)}
        >
          <ListItemText
            primary={goal.title}
            primaryTypographyProps={{ color: 'primary' }}
          />
        </ListItem>
      ))}
    </List>
  );

  return (
    <div className={classes.root}>
      {renderList()}
      <Button
        variant="contained"
        color="primary"
        onClick={() => setAddGoalOpen(true)}
      >
        Add Goal
      </Button>
      <GoalNew open={addGoalOpen} handleClose={() => setAddGoalOpen(false)} />
    </div>
  );
};

function mapStateToProps(state) {
  return { goals: state.goals };
}
export default connect(
  mapStateToProps,
  { fetchGoals },
)(withStyles(styles)(Goals));
