import React from 'react';
import { connect } from 'react-redux';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContentText from '@material-ui/core/DialogContentText';
import Button from '@material-ui/core/Button';

import { ErrorsActions } from '../state';


const ErrorDialog = ({ errors, resetErrors }) => (
  <Dialog open={errors.length > 0} aria-labelledby="form-dialog-title">
    <DialogTitle id="form-dialog-title">Error</DialogTitle>
    <DialogContent>
      {errors.map(error => (
        <DialogContentText key={error} variant="h5">
          {error}
        </DialogContentText>
      ))}
    </DialogContent>
    <DialogActions>
      <Button
        onClick={resetErrors}
        color="primary"
        variant="contained"
        size="large"
      >
        {'Ok'}
      </Button>
    </DialogActions>
  </Dialog>
);


const mapStateToProps = state => ({
  errors: state.errors,
});

const mapDispatchToProps = {
  resetErrors: ErrorsActions.resetErrors,
};


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ErrorDialog);
