export const renderTextarea = ({ input, label }) => (
  <TextField
    {...input}
    label={label}
    placeholder={label}
    fullWidth
    margin="normal"
    variant="outlined"
    multiline
    InputLabelProps={{
      shrink: true,
    }}
  />
);

export const renderSelectField = ({
  input,
  label,
  meta: { touched, error },
  children,
  ...custom
}) => (
  <FormControl error={touched && error} fullWidth>
    <InputLabel>{label}</InputLabel>
    <Select {...input} {...custom}>
      {children}
    </Select>
  </FormControl>
);

