import React, { useState, useEffect } from 'react';
import axios from 'axios';
import _ from 'lodash';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { href } from '../utils';
import Tasks from './tasks';

const Day = ({ match, history}) => {
  const [day, setDay] = useState(null);
  const [report, setReport] = useState({});
  const [newReport, setNewReport] = useState('');

  useEffect(() => {
    const { id } = match.params;
    axios({
      method: 'get',
      url: `${href}api/report/${id}/`,
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setReport(response.data.report);
      setDay(response.data.day);
    });
  }, []);

  // const renderDate = () => {
  //   if (day) {
  //     return day.date;
  //   }
  //   return 'loading';
  // };

  const addReport = () => {
    const { id } = match.params;
    axios({
      method: 'post',
      url: `${href}api/report/${id}/`,
      data: {
        content: newReport,
      },
      headers: {
        Authorization: `Token ${localStorage.getItem('token')}`,
      },
    }).then((response) => {
      setReport(response.data);
    });
  };

  const updateReport = () => {
    setNewReport(report.content);
    setReport({});
  };

  const renderReport = () => {
    if (_.isEmpty(report)) {
      return (
        <div>
          <TextField
            style={{ margin: 8 }}
            placeholder="اضف تقرير"
            fullWidth
            margin="normal"
            variant="outlined"
            multiline
            rowMin="4"
            InputLabelProps={{
              shrink: true,
            }}
            value={newReport}
            onChange={event => setNewReport(event.target.value)}
          />
          <Button color="primary" variant="contained" onClick={addReport}>
            Add Report
          </Button>
        </div>
      );
    }
    return (
      <div>
        <Button color="primary" variant="contained" onClick={updateReport}>
          Update Report
        </Button>
        <div className="col-md-10">
          <br />
          <pre className="report">{report.content}</pre>
          <br />
        </div>
      </div>
    );
  };

  if (!day) {
    return <h2>loading...</h2>;
  }
  const { id } = match.params;
  return (
    <div
      style={{
        textAlign: 'right',
        direction: 'rtl',
        width: '95%',
        maxWidth: 900,
        marginLeft: 'auto',
        marginRight: 'auto',
      }}
    >
      <Tasks day={id} date={day.date} history={history} />
      <br />
      <br />
      {renderReport()}
    </div>
  );
};

export default Day;
